import React from "react";
import styles from "../styles/signup.module.css";
import logo from "../assets/blackquarancation.png";

const SignUp = (props) => {
    return(
        <div>
            <form>
                <label for="image">
                    <img className={styles.logo} src={logo} alt="logo"/>
                </label>
                <label for="fname">Email:</label><br/>
                <input type="email" id="fname" name="fname" /><br/>
                <label for="lname">Password:</label><br/>
                <input type="password" id="lname" name="lname" />
                <label for="fname">Full Name:</label><br/>
                <input type="text" id="fname" name="fname" /><br/>
                <label for="lname">Date of Birth:</label><br/>
                <input type="date" id="lname" name="lname" />
                <label for="fname">Address:</label><br/>
                <input type="text" id="fname" name="fname" /><br/>
                <label for="lname">City:</label><br/>
                <input type="text" id="lname" name="lname" />
                <label for="fname">Province:</label><br/>
                <input type="text" id="fname" name="fname" /><br/>
            </form>
        </div>
    );
    

};

export {SignUp}